<template>
  <div>
    <vue-headful title="" />
    <main class="content container">
      <div class="content__top">
        <ul class="breadcrumbs">
          <li class="breadcrumbs__item">
            <router-link class="breadcrumbs__link" :to="{ name: 'main' }">  </router-link>
          </li>
          <li class="breadcrumbs__item">
            <a class="breadcrumbs__link">  </a>
          </li>
        </ul>

        <div class="content__row">
          <h1 class="content__title"></h1>
          <span class="content__info"> {{ productCount }} </span>
        </div>
      </div>

      <section class="cart">
        <form class="cart__form form" action="#" method="POST">
          <div class="cart__field">
            <div class="cart__list" v-if="this.$store.state.cartLoading">
              <img width="100" height="100" src="img/spinner.gif" alt="spinner" />
            </div>
            <div class="cart__list" v-else-if="this.$store.state.cartLoadingFailed">
               
            </div>
            <ul class="cart__list" v-else>
              <CartItem v-for="item in products" :item="item" :key="item.basketItemId" />
            </ul>
          </div>

          <div class="cart__block">
            <p class="cart__desc">&nbsp;   &nbsp; </p>
            <p class="cart__price">
              : <span>{{ totalPricePretty }} </span>
            </p>
            <router-link v-slot="{ navigate }" :to="{ name: 'order' }" custom>
              <button
                class="cart__button button button--primery"
                type="button"
                @click="navigate"
                v-show="products.length > 0"
              >
                 
              </button>
            </router-link>
          </div>
        </form>
      </section>
    </main>
  </div>
</template>
<script>
import CartItem from '@/components/CartItem.vue';
import { mapGetters } from 'vuex';
import numberFormat from '@/helpers/numberFormat';
import productWord from '@/helpers/productWord';

export default {
  computed: {
    ...mapGetters({ products: 'cartDetailProducts', totalPrice: 'cartTotalPrice' }),
    productCount() {
      return `${this.$store.state.cartProducts.length} ${productWord(
        this.$store.state.cartProducts.length,
      )}`;
    },
    totalPricePretty() {
      return numberFormat(this.totalPrice);
    },
  },
  components: { CartItem },
};
</script>
