<template>
  <li class="catalog__item">
    <router-link
      class="catalog__pic"
      :to="{ name: 'product', params: { id: product.id, color: currentColor } }"
    >
      <img :src="image" :alt="product.title" />
    </router-link>

    <h3 class="catalog__title">
      <a href="#">{{ product.title }}</a>
    </h3>

    <span class="catalog__price">{{ pricePretty }} </span>

    <ul class="colors colors--black" v-if="product.colors.length > 0">
      <li class="colors__item" v-for="color in product.colors" :key="color.color.id">
        <label class="colors__label">
          <input
            class="colors__radio sr-only"
            type="radio"
            :value="color.color.id"
            v-model="currentColor"
          />
          <span
            class="colors__value"
            :style="`background-color: ${
              color.color.code === '#ffffff' ? '#f0f0f0' : color.color.code
            }`"
          ></span>
        </label>
      </li>
    </ul>
  </li>
</template>
<script>
import numberFormat from '@/helpers/numberFormat';

export default {
  data() {
    return {
      currentColor: this.product.colors.length ? this.product.colors[0].color.id : '',
      image: this.product.colors[0].gallery ? this.product.colors[0].gallery[0].file.url : '',
    };
  },
  computed: {
    pricePretty() {
      return numberFormat(this.product.price);
    },
  },
  watch: {
    currentColor(value) {
      const color = this.product.colors.find((item) => item.color.id === value);
      this.image = color.gallery ? color.gallery[0].file.url : '';
    },
  },
  props: ['product'],
};
</script>
