<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="item.product.img" width="120" height="120" :alt="item.product.title" />
    </div>
    <h3 class="product__title">{{ item.product.title }}</h3>
    <p class="product__info product__info--color">
      :
      <span>
        <i
          :style="`background-color: ${
            item.product.color.code === '#ffffff' ? '#f0f0f0' : item.product.color.code
          }`"
        ></i>
        {{ item.product.color.title }}
      </span>
    </p>
    <p class="product__info product__info--size">
      :
      <span>
        {{ item.product.sizeTitle }}
      </span>
    </p>
    <span class="product__code"> : {{ item.productId }} </span>

    <div class="product__counter form__counter">
      <button type="button" aria-label="  " @click="amount--">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>

      <input type="text" v-model.number="amount" name="count" />

      <button type="button" aria-label="  " @click="amount++">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>

    <b class="product__price"> {{ amountsPretty }}  </b>

    <button
      class="product__del button-del"
      type="button"
      aria-label="   "
      @click.prevent="deleteProduct(item.basketItemId)"
    >
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>
<script>
import numberFormat from '@/helpers/numberFormat';
import { mapActions } from 'vuex';

export default {
  props: ['item'],
  computed: {
    amountsPretty() {
      return numberFormat(this.item.product.price * this.item.amount);
    },
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        if (value < 1) return;
        this.$store.dispatch('updateCartProductAmount', {
          basketItemId: this.item.basketItemId,
          amount: value,
        });
      },
    },
  },
  methods: {
    ...mapActions({ deleteProduct: 'deleteCartProduct' }),
  },
};
</script>
<style>
.product {
  grid-template-rows: repeat(4, -webkit-min-content);
  grid-template-rows: repeat(4, min-content);
}
.product__info--size {
  grid-row: 3/4;
}
.product__code {
  grid-row: 4/5;
}

.product__pic {
  grid-row: 1/5;
}
</style>
