import Vue from 'vue';
import vueHeadful from 'vue-headful';
import App from './App.vue';
import router from './router';
import store from './store';

Vue.component('vue-headful', vueHeadful);

Vue.config.productionTip = false;

new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount('#app');
