<template>
  <div>
    <vue-headful title="" />
    <main class="content container">
      <div class="content__top">
        <ul class="breadcrumbs">
          <li class="breadcrumbs__item">
            <router-link class="breadcrumbs__link" :to="{ name: 'main' }">  </router-link>
          </li>
          <li class="breadcrumbs__item">
            <router-link class="breadcrumbs__link" :to="{ name: 'cart' }">  </router-link>
          </li>
          <li class="breadcrumbs__item">
            <a class="breadcrumbs__link">   </a>
          </li>
        </ul>

        <div class="content__row">
          <h1 class="content__title"> </h1>
        </div>
      </div>

      <section class="cart">
        <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
          <div class="cart__field">
            <div class="cart__data">
              <BaseFormText
                v-model="formData.name"
                title=""
                :error="formError.name"
                placeholder="   "
              />
              <BaseFormText
                v-model="formData.address"
                title=" "
                :error="formError.address"
                placeholder="  "
              />
              <BaseFormText
                v-model="formData.phone"
                type="tel"
                title=""
                :error="formError.phone"
                placeholder="  "
              />
              <BaseFormText
                v-model="formData.email"
                type="email"
                title="Email"
                :error="formError.email"
                placeholder="  Email"
              />

              <BaseFormTextArea
                v-model="formData.comment"
                title="  "
                :error="formError.comment"
                placeholder=" "
              />
            </div>

            <div class="cart__options">
              <h3 class="cart__title"></h3>

              <ul class="cart__options options">
                <li
                  class="options__item"
                  v-for="(delivery, index) in deliveries"
                  :key="delivery.id"
                >
                  <label class="options__label">
                    <input
                      class="options__radio sr-only"
                      type="radio"
                      name="delivery"
                      :value="delivery.id"
                      v-model="deliveryTypeId"
                    />
                    <span class="options__value">
                      {{ delivery.title }}
                      <b>{{
                        deliveryPretty[index] === '0' ? '' : `${deliveryPretty[index]}  `
                      }}</b>
                    </span>
                  </label>
                </li>
              </ul>

              <h3 class="cart__title"></h3>
              <img
                width="100"
                height="100"
                src="img/spinner.gif"
                alt="spinner"
                v-if="paymentsLoading"
              />
              <ul class="cart__options options" v-else>
                <li class="options__item" v-for="payment in payments" :key="payment.id">
                  <label class="options__label">
                    <input
                      class="options__radio sr-only"
                      type="radio"
                      name="pay"
                      :value="payment.id"
                      v-model="paymentTypeId"
                    />
                    <span class="options__value"> {{ payment.title }}   </span>
                  </label>
                </li>
              </ul>
            </div>
          </div>
          <div class="cart__block" v-if="this.$store.state.cartLoading">
            <img width="100" height="100" src="img/spinner.gif" alt="spinner" />
          </div>
          <div class="cart__block" v-else-if="this.$store.state.cartLoadingFailed">
             
          </div>
          <div class="cart__block" v-else>
            <ul class="cart__orders">
              <li class="cart__order" v-for="(item, index) in products" :key="index">
                <h3>{{ item.product.title }}</h3>
                <b>{{ pricePretty[index] }} </b>
                <span>: {{ item.productId }}</span>
              </li>
            </ul>

            <div class="cart__total">
              <p>
                :
                <b>{{ deliveryPricePretty === '0' ? '' : `${deliveryPricePretty}  ` }}</b>
              </p>
              <p>
                : <b>{{ length }}</b> {{ prWord }}   <b>{{ totalPricePretty }} </b>
              </p>
            </div>

            <button class="cart__button button button--primery" type="submit">
               
            </button>
          </div>
          <img width="100" height="100" src="img/spinner.gif" alt="spinner" v-show="formLoading" />
          <div v-if="formErrorMessage" class="cart__error form__error-block">
            <h4>  !</h4>
            <p>{{ formErrorMessage }}</p>
          </div>
        </form>
      </section>
    </main>
  </div>
</template>

<script>
import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextArea from '@/components/BaseFormTextArea.vue';
import { API_BASE_URL } from '@/config';
import axios from 'axios';
import { mapGetters } from 'vuex';
import numberFormat from '@/helpers/numberFormat';
import productWord from '@/helpers/productWord';

export default {
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',
      formLoading: false,
      deliveryTypeId: 0,
      paymentTypeId: 0,
      payments: [],
      paymentsLoading: false,
      paymentsLoadingFailed: false,
    };
  },
  computed: {
    ...mapGetters({ products: 'cartDetailProducts', totalPrice: 'cartTotalPrice' }),
    pricePretty() {
      return this.products.map((item) => numberFormat(item.product.price * item.amount));
    },
    deliveryPretty() {
      return this.deliveries.map((item) => numberFormat(item.price));
    },
    deliveryPricePretty() {
      const deliveryPrice = this.deliveries.find((item) => item.id === this.deliveryTypeId);

      if (deliveryPrice) {
        return numberFormat(deliveryPrice.price);
      }
      return '';
    },
    totalPricePretty() {
      const deliveryPrice = this.deliveries.find((item) => item.id === this.deliveryTypeId);
      if (deliveryPrice) {
        return numberFormat(+deliveryPrice.price + this.totalPrice);
      }
      return '';
    },
    length() {
      return this.$store.state.cartProducts.length;
    },
    deliveries() {
      return this.$store.state.deliveries;
    },
    prWord() {
      return productWord(this.length);
    },
  },
  components: { BaseFormText, BaseFormTextArea },
  methods: {
    order() {
      this.formError = {};
      this.formErrorMessage = '';
      this.formLoading = true;
      axios
        .post(
          `${API_BASE_URL}/api/orders`,
          {
            ...this.formData,
            deliveryTypeId: this.deliveryTypeId,
            paymentTypeId: this.paymentTypeId,
          },
          {
            params: {
              userAccessKey: this.$store.state.userAccessKey,
            },
          }
        )
        .then((response) => {
          this.$store.commit('resetCart');
          this.$store.commit('updateOrderInfo', response.data);
          this.$router.push({ name: 'orderInfo', params: { id: response.data.id } });
        })
        .catch((error) => {
          this.formError = error.response.data.error.request || {};
          this.formErrorMessage = error.response.data.error.message;
        })
        .then(() => {
          this.formLoading = false;
        });
    },
    loadPayments(deliveryTypeId) {
      this.paymentsLoading = true;
      this.paymentsLoadingFailed = false;
      return axios
        .get(`${API_BASE_URL}/api/payments`, {
          params: { deliveryTypeId },
        })
        .then((response) => {
          this.payments = response.data;
        })
        .catch(() => {
          this.paymentsLoadingFailed = true;
        })
        .then(() => {
          this.paymentsLoading = false;
        });
    },
  },
  watch: {
    deliveries(value) {
      if (!this.$store.state.deliveriesLoadingFailed) this.deliveryTypeId = value[0].id;
    },
    deliveryTypeId(value) {
      this.loadPayments(value).then(() => {
        this.paymentTypeId = this.payments[0].id;
      });
    },
  },
  created() {
    this.$store.dispatch('loadDeliveries');
  },
};
</script>
