<template>
  <div>
    <vue-headful title="" />
    <main class="content container">
      <div class="content__top">
        <div class="content__row">
          <h1 class="content__title"></h1>
          <span class="content__info"> 152  </span>
        </div>
      </div>
      <div class="content__catalog">
        <ProductFilter
          :price-from.sync="filterPriceFrom"
          :price-to.sync="filterPriceTo"
          :category-id.sync="filterCategoryId"
          :filtered-page.sync="page"
          :color.sync="filterColor"
          :materials-arr.sync="filterMaterials"
          :seasons-arr.sync="filterSeasons"
        />
        <section class="catalog">
          <img width="100" height="100" src="img/spinner.gif" alt="spinner" v-if="productLoading" />
          <div v-if="productLoadingFailed">
                
            <button @click.prevent="loadProducts">  </button>
          </div>
          <ProductList :products="products" />
          <BasePagination v-model="page" :count="countProducts" :per-page="productsPerPage" />
        </section>
      </div>
    </main>
  </div>
</template>

<script>
import ProductList from '@/components/ProductList.vue';
import BasePagination from '@/components/BasePagination.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
  components: { ProductList, BasePagination, ProductFilter },
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: this.$route.params.id || 0,
      filterColor: [],
      filterMaterials: [],
      filterSeasons: [],
      page: 1,
      productsPerPage: 12,
      productsData: null,
      productLoading: false,
      productLoadingFailed: false,
    };
  },
  computed: {
    products() {
      return this.productsData ? this.productsData.items : [];
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },
  methods: {
    loadProducts() {
      this.productLoading = true;
      this.productLoadingFailed = false;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios
          .get(`${API_BASE_URL}/api/products`, {
            params: {
              page: this.page,
              limit: this.productsPerPage,
              categoryId: this.filterCategoryId,
              minPrice: this.filterPriceFrom,
              maxPrice: this.filterPriceTo,
              'colorIds[]': this.filterColor
                .filter((item) => item.checked === true)
                .map((item) => item.id),
              'materialIds[]': this.filterMaterials
                .filter((item) => item.checked === true)
                .map((item) => item.id),
              'seasonIds[]': this.filterSeasons
                .filter((item) => item.checked === true)
                .map((item) => item.id),
            },
          })
          .then((response) => (this.productsData = response.data))
          .catch(() => {
            this.productLoadingFailed = true;
          })
          .then(() => {
            this.productLoading = false;
          });
      }, 0);
    },
  },
  watch: {
    page() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterColor() {
      this.loadProducts();
    },
    filterMaterials() {
      this.loadProducts();
    },
    filterSeasons() {
      this.loadProducts();
    },
    '$route.params.id': function () {
      this.filterCategoryId = this.$route.params.id || 0;
    },
  },
  created() {
    this.loadProducts();
  },
};
</script>
