export default function productWord(productLength) {
  const count = productLength.toString();
  const lastChar = count.slice(-1);
  const beforeLastChar = count.charAt(count.length - 2);
  if (beforeLastChar !== '1') {
    if (lastChar === '1') return '';
    if (lastChar > 1 && lastChar < 5) return '';
  }
  return '';
}
