<template>
  <div>
    <vue-headful title="" />
    <main class="content container" v-if="productLoading">
      <img width="100" height="100" src="img/spinner.gif" alt="spinner" />
    </main>
    <main class="content container" v-else-if="!productData">   </main>
    <main class="content container" v-else>
      <div class="content__top">
        <ul class="breadcrumbs">
          <li class="breadcrumbs__item">
            <router-link class="breadcrumbs__link" :to="{ name: 'main' }">  </router-link>
          </li>
          <li class="breadcrumbs__item">
            <router-link
              class="breadcrumbs__link"
              :to="{ name: 'category', params: { id: category.id } }"
            >
              {{ category.title }}
            </router-link>
          </li>
          <li class="breadcrumbs__item">
            <a class="breadcrumbs__link">
              {{ product.title }}
            </a>
          </li>
        </ul>
      </div>

      <section class="item">
        <div class="item__pics pics">
          <div class="pics__wrapper">
            <img width="570" height="570" :src="image" :alt="product.title" />
          </div>
          <ul class="pics__list">
            <li class="pics__item" v-for="(image, index) in images" :key="index">
              <a
                href=""
                @click.prevent="changeImage(image, index)"
                class="pics__link"
                :class="imageIndex === index ? 'pics__link--current' : ''"
              >
                <img width="98" height="98" :src="image" :alt="product.title" />
              </a>
            </li>
          </ul>
        </div>

        <div class="item__info">
          <span class="item__code">: {{ product.id }}</span>
          <h2 class="item__title">{{ product.title }}</h2>
          <div class="item__form">
            <form class="form" action="#" method="POST" @submit.prevent="addToCart">
              <div class="item__row item__row--center">
                <CountProduct v-model="productAmount" />

                <b class="item__price"> {{ pricePretty }}  </b>
              </div>

              <div class="item__row">
                <fieldset class="form__block">
                  <legend class="form__legend"></legend>
                  <ul class="colors colors--black" v-if="colors.length > 0">
                    <li class="colors__item" v-for="color in colors" :key="color.color.id">
                      <label class="colors__label">
                        <input
                          class="colors__radio sr-only"
                          type="radio"
                          :value="color.color.id"
                          v-model.number="currentColor"
                        />
                        <span
                          class="colors__value"
                          :style="`background-color: ${
                            color.color.code === '#ffffff' ? '#f0f0f0' : color.color.code
                          }`"
                        >
                        </span>
                      </label>
                    </li>
                  </ul>
                </fieldset>

                <fieldset class="form__block">
                  <legend class="form__legend"></legend>
                  <label class="form__label form__label--small form__label--select">
                    <select
                      class="form__select"
                      type="text"
                      name="category"
                      v-model.number="currentSize"
                    >
                      <option :value="size.id" v-for="size in product.sizes" :key="size.id">
                        {{ size.title }}
                      </option>
                    </select>
                  </label>
                </fieldset>
              </div>

              <button
                class="item__button button button--primery"
                type="submit"
                :disabled="productAddSending"
              >
                 
              </button>
            </form>
            <div v-show="productAdded">   </div>
            <div v-show="productAddFailed">   </div>
            <img
              width="100"
              height="100"
              src="img/spinner.gif"
              alt="spinner"
              v-show="productAddSending"
            />
          </div>
        </div>

        <div class="item__desc">
          <ul class="tabs">
            <li class="tabs__item">
              <a
                class="tabs__link"
                :class="tabIndex === 0 ? 'tabs__link--current' : ''"
                @click.prevent="changeTab(0)"
              >
                  
              </a>
            </li>
            <li class="tabs__item">
              <a
                class="tabs__link"
                href="#"
                :class="tabIndex === 1 ? 'tabs__link--current' : ''"
                @click.prevent="changeTab(1)"
              >
                  
              </a>
            </li>
          </ul>
          <TabInfo v-show="tabIndex === 0" :product="product" />
          <TabDelivery v-show="tabIndex === 1" />
        </div>
      </section>
    </main>
  </div>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import CountProduct from '@/components/CountProduct.vue';
import TabInfo from '@/components/TabInfo.vue';
import TabDelivery from '@/components/TabDelivery.vue';

import axios from 'axios';
import { API_BASE_URL } from '@/config';
import { mapActions } from 'vuex';

export default {
  data() {
    return {
      productAmount: 1,
      currentColor: '',
      currentSize: '',
      productData: null,
      productLoading: false,
      productLoadingFailed: false,
      image: '',
      images: [],
      imageIndex: 0,
      tabIndex: 0,
      productAdded: false,
      productAddSending: false,
      productAddFailed: false,
    };
  },
  computed: {
    product() {
      return this.productData;
    },
    colors() {
      return this.product.colors;
    },
    category() {
      return this.productData.category;
    },
    pricePretty() {
      return numberFormat(this.product.price);
    },
  },
  methods: {
    ...mapActions(['addProductToCart']),
    addToCart() {
      this.productAdded = false;
      this.productAddSending = true;
      this.productAddFailed = false;
      this.addProductToCart({
        productId: this.product.id,
        amount: this.productAmount,
        color: this.currentColor,
        size: this.currentSize,
      })
        .then(() => {
          this.productAdded = true;
        })
        .catch(() => {
          this.productAdded = false;
          this.productAddFailed = true;
        })
        .then(() => {
          this.productAddSending = false;
        });
    },
    loadProduct() {
      this.productLoading = true;
      this.productLoadingFailed = false;
      return axios
        .get(`${API_BASE_URL}/api/products/${this.$route.params.id}`)
        .then((response) => (this.productData = response.data))
        .catch(() => (this.productLoadingFailed = true))
        .then(() => (this.productLoading = false));
    },
    setColor() {
      const color =
        this.product.colors.find((item) => item.color.id === +this.$route.params.color) ||
        this.product.colors[0];
      this.currentColor = color.color.id;
    },
    changeImage(image, index) {
      this.imageIndex = index;
      this.image = image;
    },
    changeTab(index) {
      this.tabIndex = index;
    },
    updateImages(value) {
      const color =
        this.product.colors.find((item) => item.color.id === value) || this.product.colors[0];
      this.image = color.gallery ? color.gallery[0].file.url : '';
      this.images = color.gallery ? color.gallery.map((item) => item.file.url) : [];
    },
  },
  watch: {
    '$route.params.id': {
      handler() {
        this.loadProduct();
      },
      immediate: true,
    },
    productData() {
      this.setColor();
      this.updateImages(+this.$route.params.color);
      this.currentSize = this.product.sizes ? this.product.sizes[0].id : '';
    },
    '$route.params.color': function () {
      this.setColor();
    },
    currentColor(value) {
      this.updateImages(value);
    },
  },
  components: { CountProduct, TabDelivery, TabInfo },
};
</script>
