<template>
  <aside class="filter">
    <form class="filter__form form" action="#" method="get" @submit.prevent="submit">
      <fieldset class="form__block">
        <legend class="form__legend"></legend>
        <label class="form__label form__label--price">
          <input
            class="form__input"
            type="text"
            name="min-price"
            v-model.number="currentPriceFrom"
          />
          <span class="form__value"></span>
        </label>
        <label class="form__label form__label--price">
          <input class="form__input" type="text" name="max-price" v-model.number="currentPriceTo" />
          <span class="form__value"></span>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend"></legend>
        <label class="form__label form__label--select">
          <select
            class="form__select"
            type="text"
            name="category"
            v-model.number="currentCategoryId"
          >
            <option value="0"> </option>
            <option :value="category.id" v-for="category in categories" :key="category.id">
              {{ category.title }}
            </option>
          </select>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend"></legend>
        <ul class="colors colors--black">
          <li class="colors__item" v-for="(color, index) in colors" :key="color.id">
            <label class="colors__label">
              <input
                class="colors__radio sr-only"
                type="checkbox"
                name="color"
                :value="color.id"
                v-model="colorsChecked[index].checked"
              />
              <span
                class="colors__value"
                :style="`background-color: ${color.code === '#ffffff' ? '#f0f0f0' : color.code}`"
              >
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend"></legend>
        <ul class="check-list">
          <li class="check-list__item" v-for="(material, index) in materials" :key="material.id">
            <label class="check-list__label">
              <input
                class="check-list__check sr-only"
                type="checkbox"
                name="material"
                :value="material.id"
                v-model="materialsChecked[index].checked"
              />
              <span class="check-list__desc">
                {{ material.title }}
                <span>{{ material.productsCount }}</span>
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend"></legend>
        <ul class="check-list">
          <li class="check-list__item" v-for="(season, index) in seasons" :key="season.id">
            <label class="check-list__label">
              <input
                class="check-list__check sr-only"
                type="checkbox"
                name="collection"
                :value="season.id"
                v-model="seasonsChecked[index].checked"
              />
              <span class="check-list__desc">
                {{ season.title }}
                <span>{{ season.productsCount }}</span>
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <button class="filter__submit button button--primery" type="submit"></button>
      <button
        class="filter__reset button button--second"
        type="button"
        v-show="isFilterChanged"
        @click.prevent="reset"
      >
        
      </button>
    </form>
  </aside>
</template>
<script>
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
  data() {
    return {
      currentPriceFrom: 0,
      currentPriceTo: 0,
      currentCategoryId: 0,
      categoriesData: null,
      colorsData: null,
      materialData: null,
      seasonsData: null,
      colorsChecked: [],
      materialsChecked: [],
      seasonsChecked: [],
    };
  },
  props: [
    'priceFrom',
    'priceTo',
    'categoryId',
    'filteredPage',
    'color',
    'materialsArr',
    'seasonsArr',
  ],
  computed: {
    // currentPriceFrom: {
    //   get() {
    //     return this.priceFrom;
    //   },
    //   set(value) {
    //     this.$emit('update:priceFrom', value);
    //   },
    // },
    categories() {
      return this.categoriesData ? this.categoriesData.items : [];
    },
    colors() {
      return this.colorsData ? this.colorsData.items : [];
    },
    materials() {
      return this.materialData ? this.materialData.items : [];
    },
    seasons() {
      return this.seasonsData ? this.seasonsData.items : [];
    },
    isFilterChanged() {
      if (
        this.currentPriceFrom !== 0 ||
        this.currentPriceTo !== 0 ||
        this.currentCategoryId !== 0 ||
        this.colorsChecked.filter((item) => item.checked === true).length > 0 ||
        this.materialsChecked.filter((item) => item.checked === true).length > 0 ||
        this.seasonsChecked.filter((item) => item.checked === true).length > 0
      ) {
        return true;
      }
      return false;
    },
  },
  watch: {
    priceFrom(value) {
      this.currentPriceFrom = value;
    },
    priceTo(value) {
      this.currentPriceTo = value;
    },
    categoryId(value) {
      this.currentCategoryId = value;
    },
    color(value) {
      this.colorsChecked = value;
    },
    colorsData() {
      this.addColorsChecked();
    },
    materialData() {
      this.addMaterialsChecked();
    },
    materialsArr(value) {
      this.materialsChecked = value;
    },
    seasonsData() {
      this.addSeasonsChecked();
    },
    seasonsArr(value) {
      this.seasonsChecked = value;
    },
  },
  methods: {
    submit() {
      this.$emit('update:priceFrom', this.currentPriceFrom);
      this.$emit('update:priceTo', this.currentPriceTo);
      this.$emit('update:categoryId', this.currentCategoryId);
      this.$emit('update:color', [...this.colorsChecked]);
      this.$emit('update:materialsArr', [...this.materialsChecked]);
      this.$emit('update:seasonsArr', [...this.seasonsChecked]);
      this.$emit('update:filteredPage', 1);
    },
    reset() {
      this.$emit('update:priceFrom', 0);
      this.$emit('update:priceTo', 0);
      this.$emit('update:categoryId', 0);
      this.$emit('update:filteredPage', 1);
      this.$emit(
        'update:color',
        this.colorsChecked.map((item) => ({ id: item.id, checked: false }))
      );
      this.$emit(
        'update:materialsArr',
        this.materialsChecked.map((item) => ({ id: item.id, checked: false }))
      );
      this.$emit(
        'update:seasonsArr',
        this.seasonsChecked.map((item) => ({ id: item.id, checked: false }))
      );
      this.currentPriceFrom = 0;
      this.currentPriceTo = 0;
      this.currentCategoryId = 0;
    },
    loadCategories() {
      axios
        .get(`${API_BASE_URL}/api/productCategories`)
        .then((response) => (this.categoriesData = response.data))
        .catch(() => {
          this.categoriesData = null;
        });
    },
    loadColors() {
      axios
        .get(`${API_BASE_URL}/api/colors`)
        .then((response) => (this.colorsData = response.data))
        .catch(() => {
          this.colorsData = null;
        });
    },
    addColorsChecked() {
      this.colorsChecked = this.colorsData
        ? this.colorsData.items.map((item) => ({ id: item.id, checked: false }))
        : [];
    },
    loadMaterials() {
      axios
        .get(`${API_BASE_URL}/api/materials`)
        .then((response) => (this.materialData = response.data))
        .catch(() => {
          this.materialData = null;
        });
    },
    addMaterialsChecked() {
      this.materialsChecked = this.materialData
        ? this.materialData.items.map((item) => ({ id: item.id, checked: false }))
        : [];
    },
    loadSeasons() {
      axios
        .get(`${API_BASE_URL}/api/seasons`)
        .then((response) => (this.seasonsData = response.data))
        .catch(() => {
          this.seasonsData = null;
        });
    },
    addSeasonsChecked() {
      this.seasonsChecked = this.seasonsData
        ? this.seasonsData.items.map((item) => ({ id: item.id, checked: false }))
        : [];
    },
  },
  created() {
    this.loadCategories();
    this.loadColors();
    this.loadMaterials();
    this.loadSeasons();
  },
};
</script>
