<template>
  <div>
    <vue-headful title="" />
    <main class="content container">
      <div class="content__top">
        <ul class="breadcrumbs">
          <li class="breadcrumbs__item">
            <router-link class="breadcrumbs__link" :to="{ name: 'main' }">  </router-link>
          </li>
          <li class="breadcrumbs__item">
            <router-link class="breadcrumbs__link" :to="{ name: 'cart' }">  </router-link>
          </li>
          <li class="breadcrumbs__item">
            <a class="breadcrumbs__link">   </a>
          </li>
        </ul>

        <h1 class="content__title">
            <span> {{ $route.params.id }}</span>
        </h1>
      </div>
      <section class="cart" v-if="orderInfoLoading">
        <img width="100" height="100" src="img/spinner.gif" alt="spinner" />
      </section>
      <section class="cart" v-else-if="orderInfoLoadingFailed"> </section>
      <section class="cart" v-else>
        <form class="cart__form form" action="#" method="POST">
          <div class="cart__field">
            <p class="cart__message">
               &nbsp;  . &nbsp;   
              &nbsp; .    &nbsp; &nbsp;  
                .
            </p>

            <ul class="dictionary">
              <li class="dictionary__item">
                <span class="dictionary__key">  </span>
                <span class="dictionary__value">{{ orderInfo.name }}</span>
              </li>
              <li class="dictionary__item">
                <span class="dictionary__key">   </span>
                <span class="dictionary__value">{{ orderInfo.address }}</span>
              </li>
              <li class="dictionary__item">
                <span class="dictionary__key">  </span>
                <span class="dictionary__value">{{ orderInfo.phone }}</span>
              </li>
              <li class="dictionary__item">
                <span class="dictionary__key"> Email </span>
                <span class="dictionary__value">{{ orderInfo.email }}</span>
              </li>
              <li class="dictionary__item">
                <span class="dictionary__key">   </span>
                <span class="dictionary__value">{{ orderInfo.paymentType }}</span>
              </li>
            </ul>
          </div>

          <div class="cart__block">
            <ul class="cart__orders">
              <li class="cart__order" v-for="(item, index) in items" :key="item.id">
                <h3>{{ item.product.title }}</h3>
                <b>{{ pricePretty[index] }} </b>
                <span>: {{ item.product.id }}</span>
              </li>
            </ul>

            <div class="cart__total">
              <p>
                : <b>{{ deliveryPretty === '0' ? '' : `${deliveryPretty}  ` }}</b>
              </p>
              <p>
                : <b>{{ items.length }}</b> {{ prWord }}  
                <b>{{ totalPricePretty }} </b>
              </p>
            </div>
          </div>
        </form>
      </section>
    </main>
  </div>
</template>
<script>
import numberFormat from '@/helpers/numberFormat';
import productWord from '@/helpers/productWord';

export default {
  data() {
    return {
      orderInfoLoading: false,
      orderInfoLoadingFailed: false,
    };
  },
  computed: {
    totalPricePretty() {
      return numberFormat(this.orderInfo.totalPrice);
    },
    deliveryPretty() {
      return numberFormat(this.orderInfo.deliveryType.price);
    },
    prWord() {
      return productWord(this.items.length);
    },
    orderInfo() {
      return this.$store.state.orderInfo;
    },
    items() {
      return this.orderInfo.basket.items;
    },
    pricePretty() {
      return this.items.map((item) => numberFormat(item.price * item.quantity));
    },
  },
  created() {
    if (this.$store.state.orderInfo && this.$store.state.orderInfo === this.$route.params.id) {
      return;
    }
    this.orderInfoLoading = true;
    this.orderInfoLoadingFailed = false;
    this.$store
      .dispatch('loadOrderInfo', this.$route.params.id)
      .catch(() => {
        this.orderInfoLoadingFailed = true;
      })
      .then(() => {
        this.orderInfoLoading = false;
      });
  },
};
</script>
