import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    cartProducts: [],
    userAccessKey: null,
    cartProductsData: [],
    cartLoading: false,
    cartLoadingFailed: false,
    orderInfo: null,
    deliveries: [],
    deliveriesLoading: false,
    deliveriesLoadingFailed: false,
  },
  mutations: {
    resetCart(state) {
      state.cartProductsData = [];
      state.cartProducts = [];
    },
    updateCartProductAmount(state, { basketItemId, amount }) {
      const cartProduct = state.cartProducts.find((item) => item.basketItemId === basketItemId);
      if (cartProduct) {
        cartProduct.amount = amount;
      }
    },
    deleteCartProduct(state, basketItemId) {
      state.cartProducts = state.cartProducts.filter((item) => item.basketItemId !== basketItemId);
    },
    updateUserAccessKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },
    updateCartProductsData(state, items) {
      state.cartProductsData = items;
    },
    syncCartProducts(state) {
      state.cartProducts = state.cartProductsData.map((item) => ({
        productId: item.product.id,
        amount: item.quantity,
        color: item.color.color.id,
        size: item.size.id,
        basketItemId: item.id,
      }));
    },
    updateCartLoading(state, isCartLoading) {
      state.cartLoading = isCartLoading;
    },
    updateCartLoadingFailed(state, isCartLoadingFailed) {
      state.cartLoadingFailed = isCartLoadingFailed;
    },
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },
    updateDeliveries(state, deliveries) {
      state.deliveries = deliveries;
    },
    updateDeliveriesLoading(state, deliveriesLoading) {
      state.deliveriesLoading = deliveriesLoading;
    },
    updateDeliveriesLoadingFailed(state, deliveriesLoadingFailed) {
      state.deliveriesLoadingFailed = deliveriesLoadingFailed;
    },
  },
  getters: {
    cartDetailProducts(state) {
      return state.cartProducts.map((item) => {
        const product = state.cartProductsData.find((p) => p.id === item.basketItemId);
        if (product) {
          return {
            ...item,
            product: {
              ...product.product,
              img: product.color.gallery ? product.color.gallery[0].file.url : '',
              color: product.color.color,
              sizeTitle: product.size.title,
            },
          };
        }
        return null;
      });
    },
    cartTotalPrice(state, getters) {
      return getters.cartDetailProducts.reduce(
        (acc, item) => item.product.price * item.amount + acc,
        0
      );
    },
  },
  actions: {
    loadCart(context) {
      context.commit('updateCartLoading', true);
      context.commit('updateCartLoadingFailed', false);
      return axios
        .get(`${API_BASE_URL}/api/baskets`, {
          params: { userAccessKey: context.state.userAccessKey },
        })
        .then((response) => {
          if (!context.state.userAccessKey) {
            localStorage.setItem('userAccessKey', response.data.user.accessKey);
            context.commit('updateUserAccessKey', response.data.user.accessKey);
          }
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
        })
        .catch(() => context.commit('updateCartLoadingFailed', true))
        .then(() => context.commit('updateCartLoading', false));
    },
    addProductToCart(context, { productId, amount, color, size }) {
      return axios
        .post(
          `${API_BASE_URL}/api/baskets/products`,
          {
            productId,
            colorId: color,
            sizeId: size,
            quantity: amount,
          },
          {
            params: {
              userAccessKey: context.state.userAccessKey,
            },
          }
        )
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
        });
    },
    updateCartProductAmount(context, { basketItemId, amount }) {
      context.commit('updateCartProductAmount', { basketItemId, amount });
      return axios
        .put(
          `${API_BASE_URL}/api/baskets/products`,
          {
            basketItemId,
            quantity: amount,
          },
          {
            params: {
              userAccessKey: context.state.userAccessKey,
            },
          }
        )
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
        })
        .catch(() => {
          context.commit('syncCartProducts');
        });
    },
    deleteCartProduct(context, basketItemId) {
      context.commit('deleteCartProduct', basketItemId);
      return axios
        .delete(`${API_BASE_URL}/api/baskets/products`, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
          data: {
            basketItemId,
          },
        })
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
        })
        .catch(() => {
          context.commit('syncCartProducts');
        });
    },
    loadOrderInfo(context, orderId) {
      return axios
        .get(`${API_BASE_URL}/api/orders/${orderId}`, {
          params: { userAccessKey: context.state.userAccessKey },
        })
        .then((response) => {
          context.commit('updateOrderInfo', response.data);
        });
    },
    loadDeliveries(context) {
      context.commit('updateDeliveriesLoading', true);
      context.commit('updateDeliveriesLoadingFailed', false);
      return axios
        .get(`${API_BASE_URL}/api/deliveries`)
        .then((response) => {
          context.commit('updateDeliveries', response.data);
        })
        .catch(() => {
          context.commit('updateDeliveriesLoadingFailed', true);
        })
        .then(() => {
          context.commit('updateDeliveriesLoading', false);
        });
    },
  },
});
