<template>
  <div class="item__content">
    <h3>:</h3>

    <ol>
      <li v-for="(delivery, index) in deliveries" :key="delivery.id">
        {{ delivery.title }} 
        {{
          deliveryPretty[index] === '0'
            ? '.        '
            : `${deliveryPretty[index]}  `
        }}
      </li>
    </ol>

    <h3>:</h3>

    <p>
                .<br />
            .      ,  
        .      .<br />
                 ,    8
      800 600 90 09<br />
    </p>
  </div>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';

export default {
  computed: {
    deliveries() {
      return this.$store.state.deliveries;
    },
    deliveryPretty() {
      return this.deliveries.map((item) => numberFormat(item.price));
    },
  },
  created() {
    this.$store.dispatch('loadDeliveries');
  },
};
</script>
